
.footer-container {
  /* position: absolute; */
  left: 0;
  bottom: 0;
  right: 0;
  text-align: center;
}
.footer-content {
  border-top: 1px solid #ddd;
  height: 150px;
  background-color: whitesmoke;
  margin-top: 50px;
  padding-top: 20px;
  padding-bottom:20px;
  color: #b5b5b5;
  height:100%;
}
.footer-content iframe {
  margin-top: 10px;
}
.footer-content a {
  color: #333 !important;
  text-decoration: none;
}
.footer-link {
  margin-left: 10px;
  margin-right: 10px;
}



