.editormd-toolbar {
  width: 100%;
  min-height: 37px;
  background: #fff;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  border-bottom: 1px solid #ddd;
}

.editormd-toolbar-container {
  padding: 0 8px;
