  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.editormd-menu {
  margin: 0;
  padding: 0;
  list-style: none;
}
.editormd-menu > li {
  margin: 0;
  padding: 5px 1px;
  display: inline-block;
  position: relative;
}
.editormd-menu > li.divider {
  display: inline-block;
  text-indent: -9999px;
  margin: 0 5px;
  height: 65%;
  border-right: 1px solid #ddd;
}
.editormd-menu > li > a {
  outline: 0;
  color: #666;
  display: inline-block;
  min-width: 24px;
  font-size: 16px;
  text-decoration: none;
  text-align: center;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  -o-border-radius: 2px;
  border-radius: 2px;
  border: 1px solid #fff;
  -webkit-transition: all 300ms ease-out;
  /* Safari, Chrome */
  -moz-transition: all 300ms ease-out;
  /* Firefox 4.0~16.0 */
  transition: all 300ms ease-out;
  /* IE >9, FF >15, Opera >12.0 */
}
.editormd-menu > li > a:hover, .editormd-menu > li > a.active {
  border: 1px solid #ddd;
  background: #eee;
}
.editormd-menu > li > a > .fa {
  text-align: center;
  display: block;
  padding: 5px;
}
.editormd-menu > li > a > .editormd-bold {
  padding: 5px 2px;
  display: inline-block;
  font-weight: bold;
}
.editormd-menu > li:hover .editormd-dropdown-menu {
  display: block;
}
.editormd-menu > li + li > a {
  margin-left: 3px;
}

.editormd-dropdown-menu {
  display: none;
  background: #fff;
  border: 1px solid #ddd;
  width: 148px;
  list-style: none;
  position: absolute;
  top: 33px;
  left: 0;
  z-index: 100;
  -webkit-box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.15);
  /* Webkit browsers */
  -moz-box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.15);
  /* Firefox */
  -ms-box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.15);
  /* IE9 */
  -o-box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.15);
  /* Opera(Old) */
  box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.15);
  /* IE9+, News */
}
.editormd-dropdown-menu:before, .editormd-dropdown-menu:after {
  width: 0;
  height: 0;
  display: block;
  content: "";
  position: absolute;
  top: -11px;
  left: 8px;
  border: 5px solid transparent;
}
.editormd-dropdown-menu:before {
  border-bottom-color: #ccc;
}
.editormd-dropdown-menu:after {
  border-bottom-color: #ffffff;
  top: -10px;
}
.editormd-dropdown-menu > li > a {
  color: #666;
  display: block;
  text-decoration: none;
  padding: 8px 10px;
}
.editormd-dropdown-menu > li > a:hover {
  background: #f6f6f6;
  -webkit-transition: all 300ms ease-out;
  /* Safari, Chrome */
  -moz-transition: all 300ms ease-out;
  /* Firefox 4.0~16.0 */
  transition: all 300ms ease-out;
  /* IE >9, FF >15, Opera >12.0 */
}
.editormd-dropdown-menu > li + li {
  border-top: 1px solid #ddd;
}

.editormd-container {
  margin: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  padding: 35px 0 0;
  position: relative;
  background: #fff;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.editormd-dialog {
  color: #666;
  position: fixed;
  z-index: 99999;
  display: none;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  border-radius: 3px;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  /* Webkit browsers */
  -moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  /* Firefox */
